body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #00dbfc;
  --background-color: #141414;
}

@font-face {
  font-family: myFontRegular;
  src: url(/devflix-react/static/media/myFontRegular.672608a0.otf);
}

@font-face {
  font-family: myFontMedium;
  src: url(/devflix-react/static/media/myFontMedium.1735b808.otf);
}

@font-face {
  font-family: myFontBold;
  src: url(/devflix-react/static/media/myFontBold.e6914f49.otf);
}

body {
  background-color: #141414;
  background-color: var(--background-color);
  overflow-x: hidden;
}

.main-container {
  width: 100%;
  height: auto;
  position: relative;
}

.header-container {
  width: 100vw;
  margin-bottom: 3vw;
  background-color: #00000000;
  transition: background-color 0.5s linear;
}

.header {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 4%;
  height: 68px;
  background: linear-gradient(#000000 0%, #00000000 100%);
}

.header-logo {
  width: 92.5px;
  margin-right: 3%;
}

.header-links {
  display: flex;
}

.header-links > div {
  margin-left: 20px;
  color: #eeeeee;
  font-family: myFontRegular;
  transition: color 0.2s;
}

.header-links > div:not(.selected-link) {
  cursor: pointer;
}

.selected-link {
  font-weight: 700;
  color: #ffffff;
  cursor: default;
}

.header-links > div:not(.selected-link):hover {
  color: #aaaaaa;
}

.video-container {
  width: 100%;
  overflow: hidden;
  position: relative;
  display: none;
}

.video-overlay {
  position: absolute;
  width: 29%;
  bottom: 35%;
  left: 4%;
  z-index: 2;
}

.overlay-logo {
  margin-bottom: 12px;
}

.overlay-logo > img{
  width: 75%;
  transition: width 1s;
}

.overlay-text {
  color: #ffffff;
  height: 17%;
  max-height: 180px;
  margin-bottom: 24px;
  overflow: hidden;
  transition: max-height 1s, margin-bottom 1s;
  text-shadow: 2px 2px 1px #000000cc;
}

.overlay-text-header {
  font-size: 2vw;
}

.overlay-text-body {
  font-size: 1.2vw;
}

.overlay-buttons {
  display: flex;
}

.overlay-buttons > div {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 0;
  height: 10%;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: myFontMedium;
  font-size: 1.2vw;
}

.overlay-buttons > div:hover {
  opacity: 0.7;
}

.play-button {
  color: #000000;
  background-color: #00dbfc;
  background-color: var(--main-color);
  margin-right: 12px;
  width: 40%;
}

.info-button {
  color: #ffffff;
  background-color: #141414;
  background-color: var(--background-color);
  width: calc(60% - 12px);
}

.button-icon {
  display: flex;
  align-items: center;
  width: 1.5vw;
  margin-right: 8px;
}

.video-placeholder {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity 0.2s;
  display: flex;
}

.video-placeholder img {
  width: 100%;
}

.play-overlay {
  width: 15%;
  aspect-ratio: 1/1;
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
}

.video-info {
  width: 10vw;
  height: 4%;
  position: absolute;
  right: 0;
  bottom: 35%;
  display: flex;
  align-items: center;
}

.mute-button {
  border: 2px solid #ffffffc0;
  height: 100%;
  aspect-ratio: 1/1;
  margin-right: 1vw;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s, visibility 0.2s;
  visibility: collapse;
}

.mute-button:hover {
  background-color: #ffffff40;
}

.age-rating {
  background-color: #33333380;
  border-left: 3px solid #ffffff;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.age-container {
  width: 35%;
  height: 60%;
  margin-left: 10%;
  background-color: #ffff37;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
  font-family: myFontMedium;
  font-size: 1vw;
  padding: 0.5%;
}

.sliders-background {
  width: 100%;
  height: 18.5vw;
  position: absolute;
  top: 40vw;
  z-index: 2;
  display: none;
  background: linear-gradient(#00000000 0%, #141414 40%);
  background: linear-gradient(#00000000 0%, var(--background-color) 40%);
}

.sliders-container {
  width: 100%;
}

.slider-container {
  width: 100%;
  margin-bottom: 4%;
}

.slider-header {
  font-family: myFontRegular;
  color: #ffffff;
  padding: 0 4.5%;
  margin-bottom: 0.5%;
}

.fixed-slider-content {
  position: relative;
  overflow: hidden;
}

.scroll-slider-content {
  display: flex;
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-behavior: smooth;
  overflow-x: scroll;
  touch-action: pan-y;
}

.scroll-slider-content::-webkit-scrollbar { 
  display: none;
}

.slider-navigation {
  height: 100%;
  background-color: #000000;
  position: absolute;
  top: 0;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  opacity: 0.8%;
  cursor: pointer;
}

.left-button {
  left: 0;
  display: none;
}

.right-button {
  left: 100%;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide {
  width: 17.8%;
  aspect-ratio: 16 / 9;
  flex-shrink: 0;
  object-fit: cover;
}

.slide:first-child {
  margin-left: 4.5%;
}

.slide:last-child {
  margin-right: 4.5%;
}

.slide:not(:last-child) {
  margin-right: 0.5%;
}

@media (min-width: 100px) {
  .header-links {
    font-size: 1.3vw;
  }

  .slider-container {
    margin-bottom: 8%;
  }

  .slider-header {
    font-size: 4vw;
    padding: 0 12%;
    margin-bottom: 2%;
  }

  .slider-navigation {
    font-size: 7vw;
    width: 10%;
  }

  
  .slide {
    width: 37%;
  }

  .slide:first-child {
    margin-left: 12%;
  }
  
  .slide:last-child {
    margin-right: 12%;
  }
  
  .slide:not(:last-child) {
    margin-right: 2%;
  }
}

@media (min-width: 500px) {
  .slider-container {
    margin-bottom: 8%;
  }

  .slider-header {
    font-size: 3vw;
    padding: 0 12%;
    margin-bottom: 2%;
  }

  .slider-navigation {
    font-size: 4.75vw;
    width: 7.5%;
  }

  .slide {
    width: 27%;
  }

  .slide:first-child {
    margin-left: 8.5%;
  }
  
  .slide:last-child {
    margin-right: 1%;
  }
  
  .slide:not(:last-child) {
    margin-right: 1%;
  }
}

@media (min-width: 1000px) {
  .header-container {
    margin: 0;
    position: fixed;
    top: 0;
    z-index: 4;
  }

  .video-container {
    display: flex;
  }

  .sliders-background {
    display: block;
  }

  .sliders-container {
    position: absolute;
    top: 40vw;
    z-index: 3;
  }

  .slider-container {
    margin-bottom: 4%;
  }

  .slider-header {
    font-size: 2vw;
    padding: 0 4.5%;
    margin-bottom: 0.5%;
  }

  .slider-navigation {
    font-size: 2.5vw;
    width: 4%;
  }

  .slide {
    width: 17.8%;
  }

  .slide:first-child {
    margin-left: 4.5%;
  }
  
  .slide:last-child {
    margin-right: 4.5%;
  }
  
  .slide:not(:last-child) {
    margin-right: 0.5%;
  }
}

@media (min-width: 1200px) {
  .header-links {
    font-size: 16px;
  }
}
